*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --font-family-main: "Roboto", sans-serif;
  --cl-txt-main: #000;
  --cl-txt-second: #545454;
  --cl-txt-white: #fff;
  --cl-txt-red: #ff001b;
  --cl-txt-accent: #ff6b08;
  --bgc-main: #e5e5e5;
  --bgc-second: #f7f7f7;
  --bgc-accent: #ff6b01;
  --anim: 250ms cubic-bezier(.4, 0, .2, 1);
  --fast-anim: 200ms ease;
  --slow-anim: 350ms ease-in-out;
}

.pagination {
  max-width: 374px;
  height: 40px;
  justify-content: center;
  align-items: center;
  gap: 5px;
  margin: 10px auto;
  display: flex;
}

.pagination__currentPage {
  min-width: 40px;
  height: 40px;
  background-color: var(--bgc-accent);
  color: var(--cl-txt-white);
  border-radius: 5px;
}

.pagination__elem {
  justify-content: center;
  align-items: center;
  display: flex;
}

.pagination__elem .link {
  min-width: 40px;
  max-width: 40px;
  height: 40px;
  justify-content: center;
  align-items: center;
  display: flex;
}

.pagination__linkPrev.link, .pagination__linkNext.link {
  display: flexbox;
  text-align: center;
  background-color: var(--bgc-second);
  border-radius: 5px;
  margin-left: auto;
  margin-right: auto;
}

.pagination__icon {
  fill: var(--cl-txt-main);
}

.pagination__linkPrev > span {
  text-align: center;
  margin-left: auto;
  margin-right: auto;
  display: block;
}

html {
  scroll-behavior: smooth;
  width: 100%;
  max-width: 100%;
  overflow-x: hidden;
}

body {
  font-family: var(--font-family-main);
  letter-spacing: .04em;
  width: 100%;
  max-width: 100%;
  color: var(--cl-txt-main);
  background-color: var(--bgc-main);
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  overflow-x: hidden;
}

main {
  min-height: calc(100vh - 330px);
}

@media screen and (min-width: 768px) {
  main {
    min-height: calc(100vh - 294px);
  }
}

h1, h2, h3, p {
  margin-top: 0;
  margin-bottom: 0;
  padding-top: 0;
  padding-bottom: 0;
}

img {
  max-width: 100%;
  height: auto;
  margin: 0;
  display: block;
}

.list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.link {
  color: inherit;
  text-decoration: none;
}

@keyframes heartbeat {
  from {
    transform-origin: center;
    animation-timing-function: ease-out;
    transform: scale(1);
  }

  10% {
    animation-timing-function: ease-in;
    transform: scale(.85);
  }

  17% {
    animation-timing-function: ease-out;
    transform: scale(.95);
  }

  33% {
    animation-timing-function: ease-in;
    transform: scale(.85);
  }

  45% {
    animation-timing-function: ease-out;
    transform: scale(1);
  }
}

.container {
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (min-width: 768px) {
