}

.scroll-to-top {
  width: 20px;
  height: 20px;
  border: 1px solid var(--color-accent);
  cursor: pointer;
  transition: transform var(--animation), background-color var(--animation);
  background-color: rgba(0, 0, 0, 0);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  margin-left: auto;
  display: flex;
  position: fixed;
  bottom: 15px;
  right: 15px;
}

@media screen and (min-width: 320px) {
  .scroll-to-top {
    width: 25px;
    height: 25px;
  }
}

@media screen and (min-width: 768px) {
  .scroll-to-top {
    width: 30px;
    height: 30px;
  }
}

.scroll-to-top:hover, .scroll-to-top:focus {
  border: .5px solid var(--cl-txt-red);
  background-color: rgba(0, 0, 0, 0);
  transform: scale(1.05);
}

.arrow-top {
  fill: var(--cl-txt-red);
  width: 10px;
  height: 10px;
}

@media screen and (min-width: 320px) {
  .arrow-top {
    width: 15px;
    height: 15px;
  }
}

.scroll-to-top.is-hidden {
  display: none;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.is-hidden {
  display: none;
}

.backdrop {
  z-index: 15;
  width: 100%;
  height: 100%;
  transition: opacity var(--anim), visibility var(--anim), transform var(--anim);
  background: rgba(0, 0, 0, .2);
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

